body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

.container {
  max-width: 180rem;
  width: 90%;
  margin: 0 auto;
}
@media (min-width: 954px) {
  .container {
    width: 95%;
  }
}

.emoji {
  font-size: 4rem;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1 {
  font-size: 3.7rem;
  font-weight: 300;
  margin: 0;
}

h2 {
  font-size: 4.8rem;
  font-weight: 900;
  margin: 0;
}

h3 {
  font-size: 2.6rem;
  font-weight: 300;
}

p {
  font-size: 2.2rem;
  font-weight: 300;
}

span {
  font-size: 3rem;
}

.center-text {
  text-align: center;
}

.title {
  font-size: 3.4rem;
  font-weight: 900;
  margin: 0;
}

.hidden {
  display: none !important;
}

.position-absolute {
  position: absolute;
}

.position-relative {
  position: relative;
}

.disabled {
  background-color: rgba(0, 0, 0, 0.5);
  min-height: 100%;
  width: 100%;
  top: 0;
  left: 0;
}

.alert {
  padding: 0.4rem;
  margin-top: 0.5rem;
}
.alert p {
  text-align: center;
  margin: 0;
  font-weight: 900;
  background-color: unset !important;
  font-size: 2rem;
  line-height: 1.5;
  color: white;
}
.alert.error {
  background-color: #dc4e4f;
}

.margin-top-bar {
  margin-top: 12.8rem;
}

.center-section {
  margin: 0 auto;
  width: 90%;
}
@media (min-width: 954px) {
  .center-section {
    width: 40%;
  }
}

.btn-green-block {
  background-color: #00b894;
  color: white;
  font-weight: 400;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border: none;
  font-size: 2rem;
  width: 100%;
}
.btn-green-block:hover {
  background-color: #009f7f;
  cursor: pointer;
}

.btn-green {
  background-color: #00b894;
  color: white;
  font-weight: 400;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border: none;
  font-size: 2rem;
}
.btn-green:hover {
  background-color: #009f7f;
  cursor: pointer;
}

.btn-orange {
  background-color: #fdcb6e;
  color: white;
  font-weight: 400;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border: none;
  font-size: 2rem;
}
.btn-orange:hover {
  background-color: #fdc255;
  cursor: pointer;
}

.btn-red-block {
  background-color: #d63031;
  color: white;
  font-weight: 400;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border: none;
  font-size: 2rem;
  width: 100%;
}
.btn-red-block:hover {
  background-color: #c52728;
  cursor: pointer;
}

.btn-disabled {
  background-color: #d9d9d9;
  color: white;
  font-weight: 400;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border: none;
  font-size: 2rem;
  filter: grayscale(2);
  cursor: not-allowed !important;
}
.btn-disabled:hover {
  background-color: #cccccc;
  cursor: pointer;
}

.btn-giant-image {
  color: white;
  padding: 3rem;
}

.dark-mode {
  background-color: #333333;
}
.dark-mode aside {
  background-color: #00856b;
}
.dark-mode .pomodoro {
  background-color: #0767b2;
}
.dark-mode .tasks {
  background-color: #8b82fe;
}
.dark-mode .admin h2,
.dark-mode .admin h3,
.dark-mode .header-phrase h2,
.dark-mode .header-phrase h3,
.dark-mode .control-panel-container h2,
.dark-mode .control-panel-container h3 {
  color: white;
}
.dark-mode .form-phrase input:not([type=submit]),
.dark-mode .form-phrase textarea {
  color: white;
  background-color: #3d3d3d;
}
.dark-mode .popup {
  background-color: #333333;
}
.dark-mode .popup input:not([type=submit]) {
  color: white;
  background-color: #3d3d3d;
}
.dark-mode .popup h3,
.dark-mode .popup label {
  color: white;
}

.body-div {
  margin-top: 12rem;
  display: grid;
}
@media (min-width: 954px) {
  .body-div {
    margin-top: 15.7rem;
  }
}
@media (min-width: 954px) {
  .body-div {
    grid-template-columns: 1fr 2fr;
    gap: 1.3rem;
  }
}

.principal-header {
  position: fixed;
  top: 0;
  z-index: 1;
  width: 100%;
  background-color: #3d3d3d;
  color: white;
  -webkit-box-shadow: 0px 10px 16px -3px rgba(0, 0, 0, 0.42);
  box-shadow: 0px 10px 16px -3px rgba(0, 0, 0, 0.42);
}

.pomodoro {
  background-color: #0984e3;
  padding: 2.3rem;
}
@media (min-width: 954px) {
  .pomodoro {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1.2rem;
  }
}
.pomodoro button {
  font-size: 3rem;
}
.pomodoro div {
  display: grid;
  place-items: center;
  color: white;
  margin-bottom: 2rem;
}
@media (min-width: 954px) {
  .pomodoro div {
    margin-bottom: 0;
  }
}
.pomodoro .pomodoro-start {
  background-color: #d63031;
  min-height: 11.5rem;
}
.pomodoro .pomodoro-counter {
  min-height: 11.5rem;
  background-color: #ff7675;
  font-weight: 900;
}
.pomodoro .pomodoro-state {
  background-color: #4158D0;
  background-image: linear-gradient(43deg, #4158D0 0%, #C850C0 46%, #FFCC70 100%);
  background-size: 400% 400%;
  animation: move 10s ease-in-out infinite;
  margin-top: 2rem;
  height: 9rem;
}
@media (min-width: 954px) {
  .pomodoro .pomodoro-state {
    margin-top: 0;
    height: auto;
  }
}
@keyframes move {
  0% {
    background-position: 0 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0 50%;
  }
}

.header-tasks {
  display: block;
  padding-bottom: 2rem;
}
@media (min-width: 954px) {
  .header-tasks {
    display: grid;
    grid-template-columns: 1fr 3fr;
    align-items: center;
    padding-bottom: 0;
  }
}

.tasks {
  background-color: #a29bfe;
  color: white;
  padding: 3rem;
  padding-top: 0.4rem;
  padding-bottom: 4rem;
}
.tasks .task-container {
  display: grid;
  gap: 1rem;
  grid-template-columns: 1fr;
}
@media (min-width: 542px) {
  .tasks .task-container {
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 954px) {
  .tasks .task-container {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
@media (min-width: 1200px) {
  .tasks .task-container {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}
.tasks h3 {
  font-weight: 400;
  margin-top: 2rem;
}

.task {
  background-color: #0984e3;
  background-size: cover;
  background-position: center, center;
  padding: 2rem;
  border-radius: 1.2rem;
  transition: ease-in-out 0.1s;
}
.task p {
  margin: 0;
  padding: 0 1rem 0 1rem;
  background-color: rgba(0, 0, 0, 0.5);
}
.task:hover {
  transform: scale(1.06);
}

.controls {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.task-edit form {
  padding: 1rem;
  display: flex;
  flex-direction: column;
}
.task-edit form fieldset {
  margin: 0;
  padding: 1rem;
  background-color: rgba(0, 0, 0, 0.5);
}
.task-edit form input {
  width: 100%;
}

aside {
  background-color: #00b894;
  color: white;
  padding: 1rem;
}
aside .text-phrase-container p {
  margin-bottom: 0;
}
aside .text-phrase-container blockquote {
  margin-top: 0;
  font-size: 2rem;
}
aside .text-phrase-container .phrase-image {
  max-height: 30rem;
}
@media (min-width: 954px) {
  aside .text-phrase-container .phrase-image {
    max-height: none;
  }
}

.fixed {
  position: relative;
  overflow: hidden;
}

.overlay {
  width: 100%;
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  background-color: rgba(0, 0, 0, 0.8);
  display: grid;
  place-items: center;
}

.popup {
  width: 80%;
  background-color: white;
  padding: 0.8rem;
  padding-bottom: 3rem;
  margin: 3rem;
  color: 900;
  border-radius: 1.2rem;
  display: grid;
}
@media (min-width: 954px) {
  .popup {
    padding: 3rem;
  }
}
.popup fieldset {
  border: none;
  display: flex;
  flex-direction: column;
}
.popup fieldset input {
  padding: 1rem;
  font-size: 2rem;
  margin-bottom: 4rem;
  width: 100%;
}
.popup fieldset label {
  font-size: 2.6rem;
}

.bar h2 {
  padding: 2rem 0;
  font-size: 3rem;
}
@media (min-width: 954px) {
  .bar h2 {
    font-size: 1.8rem;
  }
}
.bar a {
  color: white;
  display: inline;
}

.principal-footer {
  margin-top: 3rem;
  background-color: #3d3d3d;
  padding: 2.5rem 0 0 0;
  color: white;
}
.principal-footer p {
  padding: 3.5rem 0 3.5rem 0;
  margin: 0;
  background-color: #333333;
}
.principal-footer .container {
  display: block;
  padding-bottom: 3rem;
}
@media (min-width: 954px) {
  .principal-footer .container {
    display: grid;
    grid-template-columns: 1fr 2fr;
    place-items: center;
  }
}
.principal-footer .container h3 {
  font-weight: 900;
  margin: 0;
}
.principal-footer .container a {
  color: white;
  display: block;
  margin: 2rem 0 2rem 0;
  font-size: 2.3rem;
}
.principal-footer .container a:last-child {
  margin-bottom: 0;
}

.admin {
  margin-bottom: 20rem;
}
.admin h2 {
  padding: 2rem 0;
  line-height: 1;
}
.admin h3 {
  font-size: 3.5rem;
}
.admin h4 {
  font-size: 3rem;
  font-weight: 400;
  line-height: 1.5;
}

.control-panel {
  background-color: #a29bfe;
  padding: 3rem;
}
@media (min-width: 954px) {
  .control-panel {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 2rem;
  }
}
.control-panel .admin-phrases {
  background-color: #ff7675;
}
.control-panel .admin-users {
  background-color: #00b894;
}
.control-panel .admin-add {
  background-color: #0984e3;
}
.control-panel .admin-phrases,
.control-panel .admin-users,
.control-panel .admin-add {
  transition: ease-in-out 0.3s;
}
.control-panel .admin-phrases:hover,
.control-panel .admin-users:hover,
.control-panel .admin-add:hover {
  transform: scale(1.03);
}
.control-panel a {
  display: block;
  color: white;
  padding: 5rem;
  margin-bottom: 3rem;
}
@media (min-width: 954px) {
  .control-panel a {
    padding: 15rem 3rem;
    margin-bottom: 0;
  }
}
.control-panel a h4 {
  font-size: 3rem;
  text-align: center;
  margin: 0;
}

.header-phrase,
.header-images {
  margin-bottom: 3rem;
}

.control-panel-phrases {
  display: block;
}
@media (min-width: 954px) {
  .control-panel-phrases {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 1rem;
  }
}
.control-panel-phrases .phrase {
  padding: 2rem;
  color: white;
  background-color: #00b894;
  margin-bottom: 3rem;
}
@media (min-width: 954px) {
  .control-panel-phrases .phrase {
    margin-bottom: 0;
  }
}
.control-panel-phrases .phrase .btn-red-block {
  height: 100%;
}

.control-panel-images {
  display: block;
  margin-bottom: 3rem;
}
@media (min-width: 954px) {
  .control-panel-images {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 1rem;
  }
}
.control-panel-images .image {
  margin-bottom: 3rem;
}
@media (min-width: 954px) {
  .control-panel-images .image {
    margin-bottom: 0;
  }
}

.form-phrase {
  padding-bottom: 30rem;
}
.form-phrase .btn-green-block {
  font-size: 3rem;
  padding: 3rem;
}
.form-phrase input:not([type=submit]),
.form-phrase label,
.form-phrase textarea {
  display: block;
  font-size: 3rem;
  margin: 2rem 0;
  width: 100%;
}
.form-phrase textarea {
  resize: none;
}